/* 
====== 
Variables 
======
*/
:root {
  --line_color: #000000;
  --back_color: #d7b23a;

  --font-normal: "Brier-Regular";
  --font-bold: "Brier-Bold";
}
/* 
====== 
Global Styles 
======
*/
@font-face {
  font-family: "Brier-Regular";
  src: url("./font/Brier-Regular.otf");
}
@font-face {
  font-family: "Brier-Bold";
  src: url("./font/Brier-Bold.otf");
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
  font-family: "Brier-Regular";
}

a {
  text-decoration: none;
}

ul li {
  list-style: none;
  position: relative;
  padding: 0 0 0 20px;
}

ul.diamond li::before {
  margin-top: 0.3rem;
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-bottom-color: #000000;
}
ul.diamond li::after {
  margin-top: 0.3rem;
  content: "";
  position: absolute;
  left: 0px;
  top: 10px;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: #000000;
}

.root-class {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
}

/* Navbar */

.navbar {
  width: 100%;
