/* Hero Section Text Effect */

.onloadpage {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.22em;
}
.onloadpage .linerow {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.linerow2 {
  /* transform: translateY(-0%); */
}

.onloadpage .line_text {
  overflow: hidden;
  font-size: clamp(3.3rem, 7vw, 7rem);
}

.line_text2 {
  padding-left: clamp(2rem, 5vw, 7rem);
}

.onloadpage .line_text .letter {
  position: relative;
  display: inline-block;
  transform: translateY(200%);
}
.onloadpage .line_text .letter::before {
  position: absolute;
  top: 0;
  left: 0;
  content: attr(data-char);
  transform: translateY(-100%);
}
.onloadpage .line_text .letter[data-char="."]::before {
  opacity: 0;
}
.onloadpage .line_text .letter[data-char="."] {
  transform: translateY(100%);
}
.onloadpage .line_text .lowercase {
  font-size: 4vw;
}

