/* Loading */

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  border: 3px solid #000000;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-bottom-color: var(--back_color);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Loading */

/* error page */

.error-page {
  height: 100%;
  background-image: url("./images/ErrorPageImg.jpeg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.error-container {
  position: absolute;
  top: 40%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* font-size: clamp(3.3rem, 60vw, 59rem); */
}

.error-button {
  position: absolute;
  left: 0;
  top: 10%;
}

.error-footer {
  position: absolute;
  padding: clamp(1rem, 2vw, 2.5rem);
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: clamp(1.3rem, 5vw, 2rem);
  animation: bottom-to-top 1.2s ease-in-out; /* Animation name, duration, timing function */
}

/* error page */

.error-container > .error-header {
  animation: bottom-to-top 1.5s ease-in-out; /* Animation name, duration, timing function */
}
